import AuthApi from "../../api/index";

const state = {
  user: null
};
const mutations = {
  SET_USER: (state, user) => {
    state.user = user;
  }
};

const actions = {
  currentUser: async context => {
    let user = await AuthApi.getCurrentUser();
    context.commit("SET_USER", user);
  }
};

const getters = {
  getUser: state => state.user
};

export default {
  // namespaced: true,
  state,
  getters,
  actions,
  mutations
};
