<template>
  <v-app>
    <Navbar :user="user"/>
    <router-view/>
  </v-app>
</template>

<script>
import Navbar from "./components/Navbar";

export default {
  name: "App",
  components: {
    Navbar
  },
  data() {
    return {
      user: null
    };
  },
  async mounted() {
    await this.$store.dispatch("currentUser");
    this.user = this.$store.getters.getUser;
  }
};
</script>
